var self;
	export default {
		onLoad() {
			self=this;
			uni.getLocation({
				type:"gcj02",
				geocode:true,
				success: function (res) {
					console.log('当前位置的地址信息：' + res.address.country);
					console.log('当前位置的地址信息：' + res.address.province);
					console.log('当前位置的地址信息：' + res.address.city);
					console.log('当前位置的地址信息：' + res.address.district);
					console.log('当前位置的地址信息：' + res.address.street);
					uni.setStorage({
					    key: 'province',
					    data: res.address.province
					});
					uni.setStorage({
					    key: 'city',
					    data: res.address.city
					});
					uni.setStorage({
					    key: 'district',
					    data: res.address.district
					});
					uni.setStorage({
					    key: 'street',
					    data: res.address.street
					});
					
				    }
			});
			uni.getStorage({
			    key: 'province',
			    success: function (res) {
			            console.log(res.data);
			        }
			});
			
		},
		onShow() {
			self=this;
			uni.getStorage({
			    key: 'portrait',
			    success: function (res) {
			       self.portrait= res.data;
			    }
			});
			uni.getStorage({
			    key: 'province',
			    success: function (res) {
			       // self.portrait= res.data;
				   console.log(res)
			    }
			});
		},
		data() {
			return {
				portrait:"../../static/166.jpg",
				developer:[
					{name:"万科城",img:"",url:""},
					{name:"精通瑞城",img:"",url:""},
					{name:"天健城",img:"",url:""}
				]
			}
		},
		methods: {
			
		}
	}
